import React from 'react';
import allProjects from '../allProjects.json';

interface NavProps {
	currentId: number;
	changeId: (id: number) => void;
}

interface dataType {
	[key: number]: {
		id: number;
		textColor: string;
		bgColor: string;
		image: string;
		title: string;
		summary: string;
		description: any;
	};
}

const Nav = (props: NavProps) => {
	const navStyle = {
		color: `white`,
	};

	const data: dataType = allProjects;

	return (
		<div className='projects-nav'>
			<button
				style={props.currentId === 0 ? navStyle : {}}
				onClick={() => props.changeId(0)}>
				{data[0].title}
			</button>
			<button
				style={props.currentId === 1 ? navStyle : {}}
				onClick={() => props.changeId(1)}>
				{data[1].title}
			</button>
			<button
				style={props.currentId === 2 ? navStyle : {}}
				onClick={() => props.changeId(2)}>
				{data[2].title}
			</button>
		</div>
	);
};

export default Nav;
